//
// UTILITIES / TYPE
//-----------------------------------------------------------------------

.u-h1 {
  font-size: 1.75rem;

  @media #{$breakpoint-medium} {
    font-size: 3rem;
    line-height: 1.25;
  }
}

.u-h4 {
  font-size: 1.125rem;
  line-height: $base-line-height;

  @media #{$breakpoint-medium} {
    font-size: 1.375rem;
  }
}

.u-text-nowrap {
  white-space: nowrap;
}

.u-text-caps {
  font-size: 1rem;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}

.u-text-center {
  text-align: center;
}

.u-text-small {
  &,
  & p {
    font-size: $small-font-size !important;
  }

  @media #{$breakpoint-medium} {
    &,
    & p {
      font-size: $small-font-size-md !important;
    }
  }
}

.u-text-large {
  font-size: 1.75rem;
}

.u-text-huge {
  font-size: 3rem;
  line-height: 1.2;
}

.u-text-fg-color {
  color: $fg-color;
}

.u-link-unstyled {
  text-decoration: none;
}
