//
// SETUP / VARIABLES
//-----------------------------------------------------------------------

// Colors
// -------------------------

$black:                     #000;
$white:                     #f5f5f5;
$blue:                      #3503b3;

$body-bg-color:             $white;
$fg-color:                  $blue;
$button-bg-color:           $blue;
$button-fg-color:           $white;
$border-color:              $blue;


// Typography
// -------------------------

$font-regular:              "Work Sans", "Helveteica Neue", Helvetica, sans-serif;

$base-font-size:            1rem;
$small-font-size:           0.875rem;
$base-font-size-md:         1.125rem;
$small-font-size-md:        1rem;
$base-line-height:          1.425;
$base-line-height-md:       1.5;


// Spacing
// -------------------------

$space-base:                $base-font-size * $base-line-height;
$space-tiny:                $space-base * 0.25;
$space-xsmall:              $space-base * 0.5;
$space-small:               $space-base * 0.75;
$space-large:               $space-base * 1.5;
$space-xlarge:              $space-base * 2;
$space-huge:                $space-base * 4;

$space-base-md:             $base-font-size-md * $base-line-height-md;
$space-tiny-md:             $space-base-md * 0.25;
$space-xsmall-md:           $space-base-md * 0.5;
$space-small-md:            $space-base-md * 0.75;
$space-large-md:            $space-base-md * 1.5;
$space-xlarge-md:           $space-base-md * 2;
$space-huge-md:             $space-base-md * 4;


// Breakpoints
// -------------------------

$breakpoint-medium:         "only screen and (min-width : 48rem)";
$breakpoint-large:          "only screen and (min-width : 60rem)";
